<template>
  <AntdBaseTable :columns="columns" :actions="actions" :dataSource="data.data" :scroll="{x: 1500}"/>
</template>
<script lang="ts" setup>
import { AntdBaseTable } from "howuse/crud/antd"
import { useDefRequest } from "@/mock/request"
import { message } from 'ant-design-vue';

const { data } = useDefRequest<{ data: [] }>({ url: "/user/list2" }, { immediate: true })

// titlelabel
const columns = [
  {
    label: "ID",
    dataIndex: "id"
  },
  {
    label: "",
    dataIndex: "username"
  },
  {
    label: "",
    dataIndex: "date"
  },
  {
    label: "",
    dataIndex: "description",
    ellipsis: true
  },
  {
    label: "",
    dataIndex: "email"
  }
]

const actions = [
  {
    label: "",
    dataIndex: "edit",
    onClick(column) {
      message.success("")
    }
  },
  {
    label: "",
    dataIndex: "delete",
    confirm: "", // confirm 
    onClick(column) {
      message.error(JSON.stringify(column))
    }
  },
  {
    label: "",
    dataIndex: "other",
    onClick(column) {
      message.success("")
    }
  },
]
</script>