<template>
  <AntdCrudForm :columns="columns" @register="registerSearch" />
  <AntdBaseTable :columns="columns" :actions="actions" :dataSource="data.data" :scroll="{x: 1500}">
    <template #title>
      <a-button type="primary" @click="addInfo"></a-button>
    </template>
  </AntdBaseTable>
  <AntdCrudFormDrawer width="450px" v-model:visible="visible" title="/" :columns="columns" @register="register"
    @submit="submit" />
</template>
<script lang="ts" setup>
import { AntdBaseTable, AntdCrudForm, useAntdCrudForm, AntdCrudFormDrawer } from "howuse/crud/antd"
import { message } from 'ant-design-vue';
import type { CrudFormOpts } from "howuse/crud/antd";
import { useDefRequest } from "@/mock/request";

const visible = ref(false)

const { data } = useDefRequest<{ data: [] }>({ url: "/user/list2" }, { immediate: true })

// titlelabel
const columns: CrudFormOpts[] = [
  {
    label: "ID",
    dataIndex: "id"
  },
  {
    label: "",
    dataIndex: "username",
    rules: [
      { required: true, message: "" },
    ]
  },
  {
    label: "",
    dataIndex: "date",
    type: "date",
    extraAttrs: {
      format: "YYYY-MM-DD",
    }
  },
  {
    label: "",
    dataIndex: "description",
    ellipsis: true,
  },
  {
    label: "",
    dataIndex: "email",
    formIgnore: false, // 
  }
]

const actions = [
  {
    label: "",
    dataIndex: "edit",
    onClick({ text, record, index, column }) {
      //  
      updateAnyValue(record)
      visible.value = true
    }
  },
  {
    label: "",
    dataIndex: "delete",
    confirm: "", // confirm 
    onClick(column) {
      message.error(JSON.stringify(column))
    }
  },
  {
    label: "",
    dataIndex: "other",
    onClick(column) {
      message.success("")
    }
  },
]

// 
const { register, updateAnyValue, updateInputValue } = useAntdCrudForm(
  {
    userSetting: {
      include: ["username", "description", "date"] // dataIndex
    },
    formSetting:
      { labelCol: { span: 4 }, wrapperCol: { span: 17 } }
  }
)

// // 
const { register: registerSearch, updateInputValue: updateInputSearchValue } = useAntdCrudForm(
  {
    userSetting: {
      span: 8,
      search: true,
      include: ["date", "username"] // dataIndex
    },
    formSetting:
      { labelCol: { span: 5 }, wrapperCol: { span: 17 } }
  }
)

function addInfo() {
  updateAnyValue({
    date: "",
    description: "",
    email: "",
    id: "",
    username: "",
  })
  visible.value = true
}

function submit(obj) {
  message.success(JSON.stringify(obj))
}
</script>