<template>
  <div>
    <AntdCrudForm :columns="searchColumns" @submit="seachHeader" @register="register">
      <template #slot="value">
        <a-input @input="(e) =>  updateSlotValue('slot', e.target.value)" :value="value"/>
      </template>
    </AntdCrudForm>
  </div>
</template>
<script lang="ts" setup>
import { AntdCrudForm, useAntdCrudForm } from "howuse/crud/antd";
import type { SearchOpts } from "howuse/crud/antd";
import { message, Upload } from 'ant-design-vue';
import { server } from "@/mock/request";

const searchColumns: SearchOpts[] = [
  {
    label: "",
    dataIndex: "username",
    defaultValue: "",
  },
  {
    label: "",
    dataIndex: "age",
    defaultValue: "18",
  },
  {
    label: "",
    dataIndex: "switch",
    type: "switch",
  },
  {
    label: "",
    dataIndex: "chui",
    type: "radio",
    extraAttrs: {
      api: server({url: "/user/chui"}),
      name: "name",
      value: "id",
      responseKey: "data", // key datakey
    },
  },
  {
    label: "",
    dataIndex: "chuimuch",
    type: "checkbox",
    extraAttrs: {
      api: server({url: "/user/chui"}),
      name: "name",
      value: "id",
      responseKey: "data", // key datakey
    },
  },
  {
    label: "",
    dataIndex: "mark",
    type: "rate",
  },
  {
    label: "",
    dataIndex: "slider",
    type: "slider",
    defaultValue: 10,
  },
  {
    label: "",
    dataIndex: "upload",
    type: "upload",
    defaultValue: [
      {
        uid: '1',
        name: 'xxx.png',
        status: 'done',
        response: 'Server Error 500', // custom error message to show
        url: 'http://www.baidu.com/xxx.png',
      }
    ],
    extraAttrs: {
      beforeUpload(file){
        if(file.size > 1024){
          message.error("1M")
          return Upload.LIST_IGNORE
        }
      },
      onChange({file}){
        if(file.status === "uploading"){
          message.warning("")
        }else{
          message.error(`${file.error}`)
        }
      }
    }
  },
  {
    label: "",
    dataIndex: "date",
    type: "date",
    extraAttrs: {
      showTime: true,
    },
  },
  {
    label: "",
    dataIndex: "dateRange",
    type: "date-range",
  },
  {
    label: "",
    dataIndex: "time",
    type: "time",
    extraAttrs: {
      showTime: true,
    },
  },
  {
    label: "",
    dataIndex: "select",
    type: "select",
    extraAttrs: {
      api: server({url: "/user/selectList"}),
      all: true, // 
      name: "name",
      value: "id",
      responseKey: "data", // key datakey
      onChange(value){
        updateInputValue("username", value)
      }
    },
  },
  {
    label: "",
    dataIndex: "select2",
    type: "select",
    extraAttrs: {
      api: server({url: "/user/selectList"}),
      name: "name",
      value: "id",
      responseKey: "data", // key datakey
      mode: "multiple",
      maxTagCount: 1,
    },
  },
  {
    label: "",
    dataIndex: "search",
    type: "select",
    extraAttrs: {
      api: server({url: "/user/selectList"}),
      name: "name",
      value: "id",
      placeholder: "",
      allowClear: true,
      onSearch(val: string){ // onSeach show-search filter-option
        server({url: "/user/selectList", data: {name: val}}).then(res=>{
          updateSelectDropdownValue("search", res.data.data || [])
        })
      }
    },
  },
  {
    label: "1",
    dataIndex: "link1",
    type: "select",
    extraAttrs: {
      api: server({url: "/area/city1"}),
      name: "name",
      value: "id",
      onChange(val: string, { name }){
        server({url: "/area/city2", data: {name}}).then(res=>{
          updateSelectDropdownAndValue("link2", res.data.data || [])
        })
      }
    },
  },
  {
    label: "2",
    dataIndex: "link2",
    type: "select",
    extraAttrs: {
      name: "name",
      value: "id",
      placeholder: ""
    },
  },
  {
    label: "tree-select",
    dataIndex: "treeSelect",
    type: "tree-select",
    extraAttrs: {
      api: server({url: "/area/city-tree"}),
      fieldNames: { value: "id", label: "name" },
      treeNodeFilterProp: "name", //  value
      showSearch: true,// 
      multiple: true, // 
    },
  },
  {
    label: "",
    dataIndex: "slot",
    type: "slot",
    defaultValue: 'slot'
  }
];

function seachHeader(result: any) {
  message.success(JSON.stringify(result, null, 2))
}
const { updateInputValue, updateSelectDropdownAndValue ,updateSelectDropdownValue, register, updateSlotValue } = useAntdCrudForm({ userSetting: { span: 6, search: true, expandNumber: 4 }, formSetting: { labelCol: {span: 6 },  wrapperCol: { span: 17 }} });
</script>
<style lang="less" scoped></style>
